/**
 * OAuth authentication utilities
 * 
 * Configure these environment variables in your .env.local:
 * - NEXT_PUBLIC_GOOGLE_CLIENT_ID: Your Google OAuth Client ID
 * - NEXT_PUBLIC_GOOGLE_REDIRECT_URI: OAuth callback URL (defaults to /api/auth/callback/google)
 * - NEXT_PUBLIC_EMAIL_SIGNIN_URL: Email sign-in page URL (defaults to /auth/signin)
 * 
 * For production, use a backend OAuth handler endpoint instead:
 * - NEXT_PUBLIC_GOOGLE_OAUTH_URL: Your backend OAuth endpoint
 */

export const handleGoogleOAuth = () => {
  if (typeof window === 'undefined') return

  // Option 1: Use backend OAuth handler (recommended for production)
  const backendOAuthUrl = process.env.NEXT_PUBLIC_GOOGLE_OAUTH_URL
  if (backendOAuthUrl) {
    window.location.href = backendOAuthUrl
    return
  }

  // Option 2: Direct Google OAuth 2.0 flow (requires client-side configuration)
  const clientId = process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID
  const redirectUri = process.env.NEXT_PUBLIC_GOOGLE_REDIRECT_URI || 
    `${window.location.origin}/api/auth/callback/google`

  if (clientId) {
    const params = new URLSearchParams({
      client_id: clientId,
      redirect_uri: redirectUri,
      response_type: 'code',
      scope: 'openid email profile',
      access_type: 'offline',
      prompt: 'consent',
    })
    
    window.location.href = `https://accounts.google.com/o/oauth2/v2/auth?${params.toString()}`
  } else {
    // Fallback: redirect to a generic auth page
    console.warn('Google OAuth not configured. Set NEXT_PUBLIC_GOOGLE_CLIENT_ID or NEXT_PUBLIC_GOOGLE_OAUTH_URL')
    window.location.href = '/auth/signin'
  }
}

export const handleEmailAuth = () => {
  if (typeof window === 'undefined') return
  
  const emailSignInUrl = process.env.NEXT_PUBLIC_EMAIL_SIGNIN_URL || '/auth/signin'
  window.location.href = emailSignInUrl
}

